.dropdown {
  position: relative;
}
.dropdown--open .dropdown__body {
  top: 100%;
  opacity: 1;
  pointer-events: all;
}
.dropdown--top .dropdown__body {
  padding-top: 0;
  padding-bottom: 10px;
  bottom: 100%;
  top: initial;
}
.dropdown__trigger {
  padding: 15px 30px;
  font-weight: 600;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: 0.06em;
  color: #000;
  position: relative;
  background-color: #fff;
  border-radius: 10px;
  transition: all 0.2s ease;
  border: 1px solid #000;
}
.dropdown__trigger:hover {
  background-color: #000;
  color: #fff;
}
.dropdown__trigger:hover:before, .dropdown__trigger:hover:after {
  border-color: transparent transparent #fff transparent;
}
.dropdown__trigger:before, .dropdown__trigger:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
  border-width: 0px 5px 6px 5px;
  border-style: solid;
  border-color: transparent transparent #000 transparent;
}
.dropdown__trigger::before {
  left: 10px;
}
.dropdown__trigger:after {
  right: 10px;
}
.dropdown__body {
  width: 100%;
  padding-top: 5px;
  position: absolute;
  top: 90%;
  transition: all 0.2s ease;
  opacity: 0;
  pointer-events: none;
}
.dropdown__list {
  width: 100%;
  background-color: #fff;
  border-radius: 10px;
}
.dropdown__item {
  padding: 5px 20px;
}
.dropdown__link {
  display: block;
  padding: 5px 10px;
  transition: all 0.2s ease;
}
.dropdown__link:hover {
  color: red;
}
.dropdown__input {
  display: none;
}

